/***/ (function(module, exports) {

	module.exports = {"data":{"post":{"html":"<p><img style=\"margin-left:auto;margin-right:auto\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2010/08/25plus.jpg\" alt=\"25+.jpg\" border=\"0\" width=\"450\" height=\"150\" /></p>\n<p>Next weekend we are finally celebrating our silver wedding anniversary. As you may recall, the actual anniversary was in <a href=\"http://www.papascott.de/archives/2008/09/08/oslo-cruise/\">September 2008</a>. So in the spirit of procrastination, the event is entitled \"Twenty-Five Plus\".</p>\n<p><a href=\"http://www.treibhaus-marxen.de/\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2010/08/NewImage.jpg\" alt=\"Treibhaus.jpg\" border=\"0\" width=\"160\" height=\"120<br />\n\" style=\"float:right\" /></a>We both have memories of stiff and boring anniversaries that we were forced to endure as children, so we are trying to do the opposite. We'll be at one of our favorite restaurants, the <a href=\"http://www.treibhaus-marxen.de/\">Treibhaus in Marxen</a>, which as the name implies is very green and casual. We're serving \"American Barbeque\", outside if the weather allows. We're celebrating in the afternoon and will be done by sunset, not partying into the wee hours of the <s>night</s> morning.</p>\n<p>We have two special visitors from the States, my mother and my cousin. My cousin has never been to Germany, so we will also be conducting a whirlwind \"German essentials in 6 days\" tour concluding with a cruise on the Rhine (special request from my mother). Our son has a class trip all next weeks, so that fits.</p>\n<p>We've also been able bring our \"Basement Project 2009\" to a belated tentative conclusion. We had hoped to have business storage space and a guest room with bath done last winter, but the bad weather put us back and, well, the painter just finished up last week. The business space still needs some electrical work and shelving, but we made a trip to Ikea yesterday, and Voila! The guest room is now livable!</p>\n<p>So now my project is to create a \"25+ Playlist\" of early '80s music for our Sonos S5 with which to entertain (or possibly torment) our guests. Can you sing along to \"Like A Virgin\"?</p>","fields":{"title":"Twenty-Five Plus","date":"15 Aug 2010","url":"https://papascott.de/2010-08-15-twenty-five-plus/"}}},"pathContext":{"slug":"/2010-08-15-twenty-five-plus/","prev":{"fields":{"title":"There and Back Again","date":"12 Jul 2010","slug":"/2010-07-12-there-and-back-again/"}},"next":{"fields":{"title":"It Was Twenty Years Ago Today","date":"01 Sep 2010","slug":"/2010-09-01-it-was-twenty-years-ago-today/"}}}}

/***/ })

